.styled-button {
    width: 451px;
    height: 80px;
    background: #41A96C;
    box-shadow: 0px 5px 4px rgba(0, 0, 0, 0.25);
    border-radius: 16px;
    font-family: Rakkas;
    font-style: normal;
    font-weight: normal;
    font-size: 35px;
    line-height: 52px;
    border: none;
    outline: none;
    margin-top: 13px;
    cursor: pointer;
}